const ContactListItem = ({ id, name, number, onRemove }) => (
  <li key={id} className={styles.contactListItem}>
    <p className={styles.contactListItem}>{name}: </p>
    <p className={styles.contactListItem}>{number}</p>
    <div className={styles.contactListItem}>
      <button type="button" className={styles.contactListItem} onClick={onRemove}>
        Delete
      </button>
    </div>
  </li>
);

export default ContactListItem;

ContactListItem.defaultProps = {
  name: 'noName',
  number: '123 45 67',
};

ContactListItem.propTypes = {
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  number: PropTypes.string.isRequired,
  onRemove: PropTypes.func,
};